.main{
  height: 100vh;
  width: 100vw;
  background-color: #ce98ff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.container{
  height: 50vh;
  width: 50vw;
  background-color: #ddb9ff;
  border-radius: 10px;
  box-shadow: 0px 0px 10px 1px #5a5a5a;
  padding: 15px;
  font-size: 20px;
}

.btn{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
}

.btn button{
  background-color: #ff00b6;
  outline: none;
  border: 2px solid black;
  padding: 6px 35px;
  font-size: 15px;
  border-radius: 5px;
  cursor: pointer;
}